___CSS_LOADER_EXPORT___.push([module.id, ".v-popper--theme-info-dropdown .v-popper__inner{background:#049}.v-popper--theme-info-dropdown .v-popper__arrow{border-color:#049}.v-popper--theme-info-tooltip{background:#049;border-color:#049}", "",{"version":3,"sources":["webpack://./resources/js/app/Pages/Defaults/Default.vue"],"names":[],"mappings":"AAgUA,gDACA,eACA,CAEA,gDACA,iBACA,CACA,8BAEA,eAAA,CADA,iBAEA","sourcesContent":["<template>\r\n  <div id=\"default\" class=\"document\">\r\n    <div class=\"w-full border-b block 2xl:flex xl:flex lg:flex md:flex flex-nowrap justify-between items-center bg-gray-50 py-1 px-2.5\">\r\n      <h1 class=\"font-semibold text-2xl\">Defaults Settings & Parameters</h1>\r\n    </div>\r\n    <div class=\"bg-white overflow-hidden1\">\r\n      <ValidationObserver ref=\"form\">\r\n        <form @submit.prevent=\"store\">\r\n          <div class=\"w-full border-b lg:flex md:flex justify-between items-center bg-gray-50 py-1 px-2.5\">\r\n            <h3 class=\"font-semibold mb-2 lg:mb-0 md:mb-0\">Default Units Of Measure</h3>\r\n            <div class=\"flex\">\r\n              <loading-button id=\"save-button\" :loading=\"form.processing\" type=\"submit\" class=\"btn-indigo-flat mr-2\">OK</loading-button>\r\n            </div>\r\n          </div>\r\n          <div class=\"grid grid-cols-1 lg:grid-cols-2 md:grid-cols-2 gap-4 p-2.5\">\r\n            <select-input v-model=\"form.baseUnits\" :rules=\"`required`\" :title=\"`Choose base system units`\" :tool-tip-text=\"helping_tips.base_units\" :error=\"form.errors.baseUnits\" class=\"selectTyp\" :required=\"true\" :label=\"'Base Units'\" @input=\"getRedefinedUnits($event, ecoParameters)\">\r\n              <option v-for=\"(system, index) in systemsOptions\" :key=\"index\" :value=\"index\">{{ system }}</option>\r\n            </select-input>\r\n            <select-input v-model=\"form.concUnits\" :rules=\"`required`\" :title=\"`Select concentration units`\" :error=\"form.errors.concUnits\" class=\"selectTyp\" :required=\"true\" :label=\"'Concentration Units'\" :tool-tip-text=\"helping_tips.concentration_units\">\r\n              <option v-for=\"(concentration_unit, index) in concentrationUnitsOptions\" :key=\"index\" :value=\"index\">{{ concentration_unit }}</option>\r\n            </select-input>\r\n          </div>\r\n          <div class=\"w-full border-b flex flex-nowrap items-center bg-gray-50 py-1 px-2.5\">\r\n            <h3 class=\"font-semibold\">Default Region and Exposure Conditions</h3>\r\n          </div>\r\n          <div class=\"grid grid-cols-1 lg:grid-cols-3 md:grid-cols-3 gap-4 p-2.5\">\r\n            <select-input id=\"location\" v-model=\"form.location\" :rules=\"`required`\" :title=\"`Select location`\" :error=\"form.errors.location\" class=\"selectTyp\" :required=\"true\" :label=\"'Location'\" :tool-tip=\"helping_tips.location\" @input=\"getSublocations($event)\">\r\n              <option v-for=\"(location, index) in locationOptions\" :key=\"index\" :value=\"index\">{{ location }}</option>\r\n            </select-input>\r\n            <select-input id=\"sub-location\" v-model=\"form.subLocation\" :rules=\"`required`\" :title=\"`Select Sub-location`\" :error=\"form.errors.subLocation\" class=\"selectTyp\" :required=\"true\" :label=\"'Sub-locations'\" :tool-tip-text=\"helping_tips.sub_locations\" @input=\"getExposures($event)\">\r\n              <option v-for=\"(sublocation, index) in sublocationOptions\" :key=\"index\" :value=\"index\">{{ sublocation }}</option>\r\n            </select-input>\r\n            <select-input id=\"exposure\" v-model=\"form.exposureType\" :rules=\"`required`\" :title=\"`Select exposure type`\" :error=\"form.errors.exposureType\" class=\"selectTyp\" :required=\"true\" :label=\"'Exposure'\" :tool-tip-text=\"helping_tips.exposure_default_settings\">\r\n              <option v-for=\"(exposure, index) in exposureOptions\" :key=\"index\" :value=\"index\">{{ exposure }}</option>\r\n            </select-input>\r\n          </div>\r\n          <div class=\"w-full border-b flex flex-nowrap items-center bg-gray-50 py-1 px-2.5\">\r\n            <h3 class=\"font-semibold\">Economic Parameters</h3>\r\n          </div>\r\n          <div class=\"grid grid-cols-1 lg:grid-cols-4 md:grid-cols-4 gap-4 p-2.5\">\r\n            <number-input v-model=\"form.baseYear\" :tool-tip-text=\"helping_tips.base_year_default_settings\" :min=\"2000\" :max=\"`${new Date().getFullYear() + 1}`\" :rules=\"`required|min_value:2000|max_value:${new Date().getFullYear() + 1}`\" :title=\"`Allowable range of values is [2000 , ${new Date().getFullYear() + 1}]`\" step=\"1\" :error=\"form.errors.baseYear\" :required=\"true\" class=\"selectTyp\" label=\"Base Year (e.g. 2021)\" @blur=\"validateAndUpdateEcoParams('baseYear', 'Base Year', $event, 2000, new Date().getFullYear() + 1)\" />\r\n            <number-input v-model=\"form.studyPeriod\" :tool-tip-text=\"helping_tips.analysis_period_default_settings\" :min=\"1\" :max=\"500\" :rules=\"`required|min_value:1|max_value:500`\" :title=\"`Allowable range of values is [1 , 500]`\" step=\"1\" :error=\"form.errors.studyPeriod\" :required=\"true\" class=\"selectTyp mb-2.5\" label=\"Analysis Period (yrs)\" @blur=\"validateAndUpdateEcoParams('studyPeriod', 'Study Period', $event, 1, 500)\" />\r\n            <number-input v-model=\"form.inflation\" :tool-tip-text=\"helping_tips.inflation_rate_default_settings\" :min=\"0\" :max=\"20\" :rules=\"`required|min_value:0|max_value:20`\" :title=\"`Allowable range of values is [0% , 20%]`\" step=\"any\" :error=\"form.errors.inflation\" :required=\"true\" class=\"selectTyp mb-2.5\" label=\"Inflation Rate (%)\" @blur=\"validateAndUpdateEcoParams('inflation', 'Inflation', $event, 0, 20)\" />\r\n            <number-input v-model=\"form.discount\" :tool-tip-text=\"helping_tips.discount_rate_default_settings\" :min=\"0\" :max=\"20\" :rules=\"`required|min_value:0|max_value:20`\" :title=\"`Allowable range of values is [0% , 20%]`\" step=\"any\" :error=\"form.errors.discount\" :required=\"true\" class=\"selectTyp mb-2.5\" label=\"Discount Rate (%)\" @blur=\"validateAndUpdateEcoParams('discount', 'Discount Rate', $event, 0, 20)\" />\r\n          </div>\r\n          <div class=\"grey-bg-block grid grid-cols-1 lg:grid-cols-3 md:grid-cols-2 gap-4 p-2.5\">\r\n            <div class=\"colGrey\">\r\n              <h5 class=\"w-full font-semibold\"><u>Concrete & Steel</u></h5>\r\n              <number-input v-model=\"form.baseMixCost\" :tool-tip-text=\"helping_tips.concrete_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.baseMixCost\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Concrete ($/' + volume_unit + ')'\" @blur=\"validateAndUpdateEcoParams('baseMixCost', 'Base Mix Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.costBlackSteel\" :tool-tip-text=\"helping_tips.black_steel_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costBlackSteel\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Black Steel ($/' + weight_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costBlackSteel', 'Black Steel Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.costEpoxy\" :tool-tip-text=\"helping_tips.epoxy_coated_steel_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costEpoxy\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Epoxy Coated Steel ($/' + weight_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costEpoxy', 'Epoxy Steel Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.costStainless\" :tool-tip-text=\"helping_tips.stainless_steel_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costStainless\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Stainless Steel ($/' + weight_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costStainless', 'Stainless Steel Cost', $event, 0, 10000)\" />\r\n            </div>\r\n            <div class=\"colGrey\">\r\n              <h5 class=\"pr-5 w-full font-semibold\"><u>Barriers & Inhibitors</u></h5>\r\n              <number-input v-model=\"form.costMembrane\" :tool-tip-text=\"helping_tips.membrane_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costMembrane\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Membrane ($/' + area_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costMembrane', 'Membrane Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.costSealer\" :tool-tip-text=\"helping_tips.sealer_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costSealer\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Sealer ($/' + area_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costSealer', 'Sealer Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.costInhibitor\" :tool-tip-text=\"helping_tips.inhibitor_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.costInhibitor\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Inhibitor ($/' + capacity_unit + ')'\" @blur=\"validateAndUpdateEcoParams('costInhibitor', 'Inhibitor Cost', $event, 0, 10000)\" />\r\n            </div>\r\n            <div class=\"colGrey\">\r\n              <h5 class=\"selectTyp font-semibold\"><u>Repairs</u></h5>\r\n              <number-input v-model=\"form.repairCost\" :tool-tip-text=\"helping_tips.repair_default_settings\" :min=\"0\" :max=\"10000\" :rules=\"`required|min_value:0|max_value:10000`\" :title=\"`Allowable range of values is [0 , 10000]`\" step=\"any\" :error=\"form.errors.repairCost\" :required=\"true\" class=\"selectTyp mb-2.5\" :label=\"'Repair ($/' + area_unit + ')'\" @blur=\"validateAndUpdateEcoParams('repairCost', 'Repair Cost', $event, 0, 10000)\" />\r\n              <number-input v-model=\"form.areaToRepairPct\" :tool-tip-text=\"helping_tips.area_to_repair_default_settings\" :min=\"0.1\" :max=\"100\" :rules=\"`required|min_value:0.1|max_value:100`\" :title=\"`Allowable range of values is [0.1 , 100]`\" step=\"any\" :error=\"form.errors.areaToRepairPct\" :required=\"true\" class=\"selectTyp mb-2.5\" label=\"Area to repair (%)\" @blur=\"validateAndUpdateEcoParams('areaToRepairPct', 'Area to repair percentage', $event, 0.1, 100)\" />\r\n              <number-input v-model=\"form.repairIntervalYrs\" :tool-tip-text=\"helping_tips.fixed_repair_intervals_default_settings\" :min=\"1\" :max=\"500\" :rules=\"`required|min_value:1|max_value:500`\" :title=\"`Allowable range of values is [1 , 500]`\" step=\"1\" :error=\"form.errors.repairIntervalYrs\" :required=\"true\" class=\"selectTyp mb-2.5\" label=\"Fixed repair intervals (yrs)\" @blur=\"validateAndUpdateEcoParams('repairIntervalYrs', 'Repair Interval', $event, 1, 500)\" />\r\n            </div>\r\n          </div>\r\n          <div class=\"w-full border-b flex flex-nowrap items-center bg-gray-50 py-1 px-2.5\">\r\n            <h3 class=\"font-semibold\">Colors</h3>\r\n          </div>\r\n          <div class=\"p-2.5 circlesBlock justify-between flex flex-wrap items-center\">\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Base Case</h5>\r\n              <v-swatches v-model=\"form.base_alt_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Alternative 1</h5>\r\n              <v-swatches v-model=\"form.alt1_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Alternative 2</h5>\r\n              <v-swatches v-model=\"form.alt2_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Alternative 3</h5>\r\n              <v-swatches v-model=\"form.alt3_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Alternative 4</h5>\r\n              <v-swatches v-model=\"form.alt4_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n\r\n            <div class=\"flex items-center justify-between mobile-flex-full mb-2 lg:mb-0 md:mb-0\">\r\n              <h5 class=\"pr-2 font-semibold\">Alternative 5</h5>\r\n              <v-swatches v-model=\"form.alt5_color\" class=\"pr-5\" shapes=\"circles\" show-border popover-x=\"right\" />\r\n            </div>\r\n          </div>\r\n          <div class=\"w-full border-b lg:flex md:flex justify-between items-center bg-gray-50 py-1 px-2.5\">\r\n            <span style=\"font-weight: 500\" class=\"btn-indigo-flat mr-1 mb-1 px-4 py-2 text-sm leading-4 text-gray-600 border reset-btn-mobile\" @click=\"resetDefaults()\">Reset to Life-365 default values</span>\r\n            <div class=\"flex\">\r\n              <loading-button id=\"save-button\" :loading=\"form.processing\" type=\"submit\" class=\"btn-indigo-flat mr-2\">OK</loading-button>\r\n            </div>\r\n          </div>\r\n        </form>\r\n      </ValidationObserver>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport swal from 'sweetalert'\r\nimport Icon from '@app/Shared/Icon'\r\nimport Layout from '@app/Shared/Layout'\r\nimport NumberInput from '@app/Shared/NumberInput'\r\nimport LoadingButton from '@app/Shared/LoadingButton'\r\nimport SelectInput from '@app/Shared/SelectInput.vue'\r\nimport VSwatches from 'vue-swatches'\r\nimport 'vue-swatches/dist/vue-swatches.css'\r\nimport 'v-tooltip/dist/v-tooltip.css'\r\nimport { EventBus } from '@app/event-bus'\r\nexport default {\r\n  metaInfo: { title: 'Default Settings' },\r\n  components: {\r\n    Icon,\r\n    NumberInput,\r\n    SelectInput,\r\n    LoadingButton,\r\n    VSwatches,\r\n  },\r\n  layout: Layout,\r\n  remember: 'form',\r\n  props: {\r\n    systems: Object,\r\n    concentrationUnits: Object,\r\n    locations: Object,\r\n    ecoparameters: Object,\r\n    baseUnits: String,\r\n    concUnits: String,\r\n    location: String,\r\n    subLocation: String,\r\n    exposureType: String,\r\n    base_alt_color: String,\r\n    alt1_color: String,\r\n    alt2_color: String,\r\n    alt3_color: String,\r\n    alt4_color: String,\r\n    alt5_color: String,\r\n    helping_tips: Object,\r\n  },\r\n  mounted() {\r\n    EventBus.$on(\"navigateApplicationTo\", (data) => {\r\n      this.form.intendedUrl = data;\r\n      EventBus.$off(\"navigateApplicationTo\");\r\n      this.store()\r\n    });\r\n  },\r\n  data() {\r\n    return {\r\n      //All Units\r\n      area_unit: null,\r\n      volume_unit: null,\r\n      capacity_unit: null,\r\n      weight_unit: null,\r\n      //Select Options\r\n      systemsOptions: this.systems,\r\n      locationOptions: this.locations,\r\n      concentrationUnitsOptions: null,\r\n      sublocationOptions: null,\r\n      exposureOptions: null,\r\n      //Form\r\n      form: this.$inertia.form({\r\n        intendedUrl: 'set-project',\r\n        baseUnits: this.baseUnits,\r\n        concUnits: this.concUnits,\r\n        //Locations specific parameters\r\n        location: this.location,\r\n        subLocation: this.subLocation,\r\n        exposureType: this.exposureType,\r\n        ecoparameters: this.ecoparameters,\r\n        //Economic parameters\r\n        baseYear: this.ecoparameters.baseYear.value,\r\n        studyPeriod: this.ecoparameters.studyPeriod.value,\r\n        inflation: parseFloat(this.ecoparameters.inflation.value).toFixed(2),\r\n        discount: parseFloat(this.ecoparameters.discount.value).toFixed(2),\r\n        baseMixCost: parseFloat(this.ecoparameters.baseMixCost.value).toFixed(2),\r\n        costBlackSteel: parseFloat(this.ecoparameters.costBlackSteel.value).toFixed(2),\r\n        costEpoxy: parseFloat(this.ecoparameters.costEpoxy.value).toFixed(2),\r\n        costStainless: parseFloat(this.ecoparameters.costStainless.value).toFixed(2),\r\n        costMembrane: parseFloat(this.ecoparameters.costMembrane.value).toFixed(2),\r\n        costSealer: parseFloat(this.ecoparameters.costSealer.value).toFixed(2),\r\n        costInhibitor: parseFloat(this.ecoparameters.costInhibitor.value).toFixed(2),\r\n        repairCost: parseFloat(this.ecoparameters.repairCost.value).toFixed(2),\r\n        areaToRepairPct: parseFloat(this.ecoparameters.areaToRepairPct.value).toFixed(2),\r\n        repairIntervalYrs: this.ecoparameters.repairIntervalYrs.value,\r\n        //Colors\r\n        base_alt_color: this.base_alt_color,\r\n        alt1_color: this.alt1_color,\r\n        alt2_color: this.alt2_color,\r\n        alt3_color: this.alt3_color,\r\n        alt4_color: this.alt4_color,\r\n        alt5_color: this.alt5_color,\r\n      }),\r\n    }\r\n  },\r\n  computed: {\r\n    ecoParameters() {\r\n      return this.$store.state.ecoParameters\r\n    },\r\n  },\r\n  created() {\r\n    let eps = this.$props.ecoparameters\r\n    let system = this.$props.baseUnits\r\n    let defaultLocation = this.$props.location\r\n    this.$store.state.currentSystem = system\r\n    this.getRedefinedUnits(system, eps)\r\n    this.getSublocations(defaultLocation)\r\n  },\r\n  methods: {\r\n    store() {\r\n      this.$refs.form.validate().then((success) => {\r\n        if (!success) {\r\n          return\r\n        }\r\n        this.form.post(this.route('save.defaults'))\r\n      })\r\n    },\r\n    resetDefaults() {\r\n      this.$store.state.currentSystem = null\r\n      this.$store.state.ecoParameters = null\r\n      window.location.replace('/reset-defaults')\r\n    },\r\n    getRedefinedUnits(value, eps) {\r\n      this.$store.state.ecoParameters = eps\r\n      axios\r\n        .post('/redefine-units', {\r\n          current_system_identifier: this.$store.state.currentSystem,\r\n          new_system_identifier: value,\r\n          current_conc_unit: this.form.concUnits,\r\n          data: eps,\r\n        })\r\n        .then((response) => {\r\n          const { data } = response\r\n          //Storing to state\r\n          this.$store.state.currentSystem = data.current_system\r\n          this.$store.state.ecoParameters = data.ecoparameters\r\n          //Setting data values\r\n          this.concentrationUnitsOptions = data.conc_units\r\n          this.area_unit = data.area_unit\r\n          this.volume_unit = data.volume_unit\r\n          this.weight_unit = data.weight_unit\r\n          this.capacity_unit = data.capacity_unit\r\n          //Setting form values\r\n          this.form.concUnits = data.default_conc_unit\r\n          this.form.ecoparameters = data.ecoparameters\r\n          this.form.baseYear = data.ecoparameters.baseYear.value\r\n          this.form.studyPeriod = data.ecoparameters.studyPeriod.value\r\n          this.form.inflation = parseFloat(data.ecoparameters.inflation.value).toFixed(2)\r\n          this.form.discount = parseFloat(data.ecoparameters.discount.value).toFixed(2)\r\n          this.form.baseMixCost = parseFloat(data.ecoparameters.baseMixCost.value).toFixed(2)\r\n          this.form.costBlackSteel = parseFloat(data.ecoparameters.costBlackSteel.value).toFixed(2)\r\n          this.form.costEpoxy = parseFloat(data.ecoparameters.costEpoxy.value).toFixed(2)\r\n          this.form.costStainless = parseFloat(data.ecoparameters.costStainless.value).toFixed(2)\r\n          this.form.costMembrane = parseFloat(data.ecoparameters.costMembrane.value).toFixed(2)\r\n          this.form.costSealer = parseFloat(data.ecoparameters.costSealer.value).toFixed(2)\r\n          this.form.costInhibitor = parseFloat(data.ecoparameters.costInhibitor.value).toFixed(2)\r\n          this.form.repairCost = parseFloat(data.ecoparameters.repairCost.value).toFixed(2)\r\n          this.form.areaToRepairPct = parseFloat(data.ecoparameters.areaToRepairPct.value).toFixed(2)\r\n          this.form.repairIntervalYrs = data.ecoparameters.repairIntervalYrs.value\r\n        })\r\n    },\r\n    assignIntendedUrl(){\r\n      alert('sample');\r\n      this.form.intendedUrl = document.getElementById('navigateApplicationTo').value\r\n      this.store()\r\n    },\r\n    getSublocations(value) {\r\n      axios.get(`/get-sublocations/${value}`).then((response) => {\r\n        const { data } = response\r\n        this.sublocationOptions = data.sublocations\r\n        this.form.sublocation = data.default_value\r\n        this.getExposures()\r\n      })\r\n    },\r\n    getExposures() {\r\n      axios.get(`/get-exposures/${this.form.subLocation}`).then((response) => {\r\n        const { data } = response\r\n        this.exposureOptions = data.exposures\r\n        if (Object.values(this.exposureOptions).indexOf(this.form.exposureType) < 0) {\r\n          this.form.exposureType = data.default_value\r\n        }\r\n      })\r\n    },\r\n    validateAndUpdateEcoParams(ecoparameter, field, entry, min, max) {\r\n      let temporaryValue = null\r\n      Object.keys(this.$store.state.ecoParameters).forEach(function (key, index) {\r\n        if (key === ecoparameter) {\r\n          if (entry && entry > max) {\r\n            swal('Allowable range of values for ' + field + ' is [' + min + ' , ' + max + ']')\r\n            entry = max\r\n          } else if (entry && entry < min) {\r\n            swal('Allowable range of values for ' + field + ' is [' + min + ' , ' + max + ']')\r\n            entry = min\r\n          }\r\n          if (ecoparameter == 'baseYear' || ecoparameter == 'studyPeriod' || ecoparameter == 'repairIntervalYrs') {\r\n            temporaryValue = this[key].value = entry\r\n          } else {\r\n            temporaryValue = this[key].value = parseFloat(entry).toFixed(2)\r\n          }\r\n        }\r\n      }, this.$store.state.ecoParameters)\r\n      this.form[ecoparameter] = temporaryValue\r\n    },\r\n  },\r\n}\r\n\r\n</script>\r\n<style>\r\n.v-popper--theme-info-dropdown .v-popper__inner {\r\n  background: #004499;\r\n}\r\n\r\n.v-popper--theme-info-dropdown .v-popper__arrow {\r\n  border-color: #004499;\r\n}\r\n.v-popper--theme-info-tooltip {\r\n  border-color: #004499;\r\n  background: #004499;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
